
import { Options, Vue } from 'vue-class-component'
import { message } from 'ant-design-vue'
import {
  EditOutlined,
  DeleteOutlined,
  PlusOutlined
} from '@ant-design/icons-vue'
import { Album } from '@/types/Album'
import request from '@/request/AlbumRequest'
@Options({
  components: {
    EditOutlined,
    DeleteOutlined,
    PlusOutlined
  }
})
export default class Home extends Vue {
  // 相册列表
  albums: Album[] = []

  // 创建相册相关属性
  albumVisible = false
  albumConfirm = false
  albumModalTitle = '新建相册'
  formData: Album = {}

  // 加载事件
  mounted() {
    this.loadAlbums()
  }

  // 加载相册
  async loadAlbums() {
    const res = await request.listQuery()
    if (res.code === 0) {
      this.albums = res.data
    } else {
      message.error(res.msg)
    }
  }

  /**
   * 转换封面图片地址
   * @param cover 封面图片
   */
  coverSrc(cover: string) {
    return cover.startsWith('http') ? cover : '/file/download/' + cover
  }

  /**
   * 进入相册
   * @param album 相册数据
   */
  onEnter(album: Album) {
    this.$router.push({
      path: '/album',
      query: { albumId: album.id }
    })
  }

  /**
   * 编辑相册
   * @param album 相册数据
   */
  onEdit(album: Album) {
    this.formData = album
    this.albumVisible = true
    this.albumModalTitle = '编辑相册'
  }

  /**
   * 删除相册
   * @param album 相册数据
   */
  onDelete(album: Album) {
    album.id && request.deleteAlbum(album.id).then(res => {
      if (res.code === 0) {
        this.albums = this.albums.filter(item => item.id !== album.id)
      } else {
        message.error(res.msg)
      }
    })
  }

  /**
   * 点击新建相册
   */
  onCreateAlbum() {
    this.albumVisible = true
    this.albumModalTitle = '新建相册'
    this.formData = {}
  }

  /**
   * 保存相册
   */
  async onSaveAlbum() {
    const name = this.formData.name
    if (!name || /^\s+/.test(name)) {
      message.warning('请输入相册名称')
      return false
    }
    this.albumConfirm = true
    const res = await request.save(this.formData)
    if (res.code === 0) {
      this.albumVisible = false
      message.success('相册创建成功')
    } else {
      this.albumConfirm = false
      message.error('相册创建失败: ' + res.msg)
    }
  }
}
